:root {
    --gris: #ebeff2;
    --gris-oscuro: #828383;
    --azul:  #067fff;
    --blanco: #fff;

    --fuente1: 'Nunito', sans-serif;
    --fuente2: 'Roboto', sans-serif;

}

.blog {
    width: 100%;
    margin: 0 auto;
}
.blog h1 {
    text-align: center;
    font-family: var(--fuente1);
    font-weight: bold;
    font-size: 1.8em;
}
.blog article {
    background-color: var(--blanco);
    text-align: center;
    border-radius: 15px;
    box-shadow: -2px 4px 9px -6px rgba(0,0,0,1);        
    width: 90%;
    margin: 0 auto;
    padding: 20px 0;
    max-width: 1100px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.blog__text {
    width: 80%;
    margin: 0 auto;
}
.blog .blog__article h3 {
    font-size: 1.2em;
    font-weight: bold;
    color: var(--gris-oscuro);
    font-family: var(--fuente1);
    margin: 0 0 10px 0;
}
.blog .blog__article h2 {
    font-size: 1.4em;
    font-weight: bold;
    margin: 0 0 15px 0;
}
.blog .blog__article button {
    background-color: var(--azul);
    width: 100px;
    height: 30px;
    border: none;
    border-radius: 5px;
    box-shadow: -2px 4px 9px -6px rgba(0,0,0,1);
}
.blog .blog__article img {
    width: 90%;
    margin: 0 0 15px 0;
}
.blog .blog__article button a {
    color: var(--blanco);
    text-decoration: none;
    font-family: var(--fuente2);
}
@media screen and (min-width: 1024px) {
    .blog {
        font-size: 18px;
        width: 75%;
        margin: 0 auto;
    }
}
