:root {
    --gris: #ebeff2;
    --gris-oscuro: #828383;
    --azul:  #067fff;
    --blanco: #fff;

    --fuente1: 'Nunito', sans-serif;
    --fuente2: 'Roboto', sans-serif;

}

.header {
    width: 100%;
    margin: 0 auto;
    text-align: center;
    position: relative;
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.header__background {
    width: 100%;
    height: 300px;
    position: absolute;
    top: 0;
    z-index: 1;
    background: linear-gradient(180deg, rgba(89,6,94,1) 0%, rgba(9,9,121,1) 50%, rgba(6,127,255,1) 100%);
}
.header__info img {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    box-shadow: -2px 4px 9px -6px rgba(0,0,0,1);
    margin-top: 20px;
}
.header__info {
    width: 90%;
    margin: 100px auto 50px auto;
    background-color: var(--blanco); 
    border-radius: 15px;
    box-shadow: -2px 4px 9px -6px rgba(0,0,0,1);
    z-index: 2;
}
.header__info-text {
    padding: 0 50px 20px;
}
.header__info-text h2 {
    font-size: 3em;
    margin: 25px 0 10px;
}
.header__info-text h3 {
    margin: 10px 0 30px;
    font-size: 1.2em;
    font-weight: lighter;
    color: var(--gris-oscuro);
    font-family: var(--fuente1);
}
.header__info-text p {
    font-size: 1.4em;
    font-weight: lighter;
    line-height: 45px;
    word-spacing: 2px;
}
.header__info-text span {
    font-weight: bold;
    color: var(--azul)
}
.header__info-icons {
    display: flex;
    justify-content: center;
    width: 90%;
    text-align: center;
    margin: 20px auto 50px auto;
}
.header__info-icons div {
    width: 35px;
    height: 35px;
    background-size: 100%;
    background-position: center;
    background-repeat: no-repeat;
    margin: 0 15px;
}
.github {
    background-image: url(../../img/icons8-github-90.png);
}
.twitter {
    background-image: url(../../img/icons8-twitter-50.png);
}
.facebook {
    background-image: url(../../img/icons8-facebook-90.png);
}
.linkedin {
    background-image: url(../../img/icons8-linkedin-60.png);
}
