:root {
    --gris: #ebeff2;
    --gris-oscuro: #828383;
    --azul:  #067fff;
    --blanco: #fff;

    --fuente1: 'Nunito', sans-serif;
    --fuente2: 'Roboto', sans-serif;

}

.projects {
    width: 100%;
}
.projects h1 {
    font-size: 1.8em;
    margin: 0;
    margin-bottom: 35px;
    text-align: center;
    font-weight: normal;
}
.projects__articles {
    width: 80%;
    margin: 0 auto; 
    text-align: justify;   
}
.projects__articles article {
    margin: 50px auto;
}
.projects__articles article h3 {
    font-size: 1.2em;
    font-weight: bold;
    color: var(--gris-oscuro);
    font-family: var(--fuente1);
    margin: 0 0 10px 0;
}
.projects__articles article h2 {
    font-size: 1.4em;
    font-weight: bold;
    margin: 0 0 15px 0;
}
.projects__articles article p {
    font-size: 1.2em;
    font-weight: lighter;
    font-family: var(--fuente1);
    margin: 0 0 15px 0;
}
.projects__articles article button {
    background-color: var(--azul);
    width: 100px;
    height: 30px;
    border: none;
    border-radius: 5px;
    box-shadow: -2px 4px 9px -6px rgba(0,0,0,1);
}
.projects__articles article a {
    color: var(--blanco);
    text-decoration: none;
    font-family: var(--fuente2);
}