.projects {
    width: 100%;
}
.projects h1 {
    font-size: 1.8em;
    margin: 0;
    margin-bottom: 35px;
    text-align: center;
    font-weight: normal;
}
.projects__articles {
    width: 80%;
    margin: 0 auto; 
    text-align: justify;   
}
.projects__articles article {
    margin: 50px auto;
}
.projects__articles article h3 {
    font-size: 1.2em;
    font-weight: bold;
    color: #828383;
    color: var(--gris-oscuro);
    font-family: 'Nunito', sans-serif;
    font-family: var(--fuente1);
    margin: 0 0 10px 0;
}
.projects__articles article h2 {
    font-size: 1.4em;
    font-weight: bold;
    margin: 0 0 15px 0;
}
.projects__articles article p {
    font-size: 1.2em;
    font-weight: lighter;
    font-family: 'Nunito', sans-serif;
    font-family: var(--fuente1);
    margin: 0 0 15px 0;
}
.projects__articles article button {
    background-color: #067fff;
    background-color: var(--azul);
    width: 100px;
    height: 30px;
    border: none;
    border-radius: 5px;
    box-shadow: -2px 4px 9px -6px rgba(0,0,0,1);
}
.projects__articles article a {
    color: #fff;
    color: var(--blanco);
    text-decoration: none;
    font-family: 'Roboto', sans-serif;
    font-family: var(--fuente2);
}
:root {
