:root {
    --gris: #ebeff2;
    --gris-oscuro: #828383;
    --azul:  #067fff;
    --blanco: #fff;

    --fuente1: 'Nunito', sans-serif;
    --fuente2: 'Roboto', sans-serif;
}
body {
    background-color: #fff;
    background-color: var(--blanco);
    font-family: 'Roboto', sans-serif;
    font-family: var(--fuente2);
    margin: 0;
}
html {
    scroll-behavior: smooth;
    font-size: 100%;
}
main {
    width: 80%;
    margin: 80px auto;
}

span {
    font-weight: bold;
}
/* --------------------- MENU -------------------------- */
.menu-icon {
    width: 100%;
    background: none;
    position: fixed;
    z-index: 3;
    top: 10px;
    display: flex;
    justify-content: center;
}
.menu-icon img {
    width: 40px;
    -webkit-filter: drop-shadow(0 2px 5px rgba(0, 0, 0, 0.7));
            filter: drop-shadow(0 2px 5px rgba(0, 0, 0, 0.7));
}

/* --------------------- HEADER-INFO -------------------------- */
.header-info {
    text-align: center;
    margin: 50px 0;
}
.header-info h3 {
    font-size: 1.2em;
    color: #828383;
    color: var(--gris-oscuro);
    font-weight: lighter;
}
.header-info h1 {
    font-size: 2.4em;
}
.header-info .bio-info img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}
.header-info .bio-info {
    display: flex;
    justify-content: center;
    align-items: center;
}
.header-info .bio-info img {
    width: 50px;
    border-radius: 50%;
}
.bio-info-text {
    margin: 0 0 0 10px;
}
.bio-info-text p {
    margin: 0 0 5px 0;
    font-size: 1em;
}
.bio-info-text a {
    text-decoration: none;
    color: #067fff;
    color: var(--azul);
}

/* --------------------- ARTICULO -------------------------- */
article img {
    width: 100%;
    margin: 15px 0;
}
article {
    line-height: 25px;
    text-align: justify;
}
article h2 {
    text-align: left;
    font-size: 1.4em;
}
article p {
    font-size: 1em;
}

/* --------------------- FOOTER -------------------------- */
footer {
    width: 100%;
    margin: 0;
    padding: 10px 0;
    background-color: black;
    text-align: center;
    font-size: 13px;
    font-family: 'Roboto', sans-serif;
    font-family: var(--fuente2);
    font-weight: lighter;
    color: #fff;
    color: var(--blanco);
}

@media screen and (min-width: 1024px) {
    html {
        font-size: 18px;
    }
    main {
        width: 70%;
        margin: 0 auto;
    }
    article {
        line-height: 38px;
    }
}
