:root {
    --gris: #ebeff2;
    --gris-oscuro: #828383;
    --azul:  #067fff;
    --blanco: #fff;

    --fuente1: 'Nunito', sans-serif;
    --fuente2: 'Roboto', sans-serif;

}

.sections {
    width: 100%;
    background-color: var(--azul);
    margin: 0;
    margin-top: 50px;
    padding: 20px 0;
    text-align: left;
}
.sections__link {
    width: 80%;
    margin: 25px auto 25px auto;
    display: flex;
    flex-direction: column;
}
.sections__link h2 {
    font-size: 1.4em;
    color: var(--blanco);
    margin: 0;
    font-family: var(--fuente1);
}
.sections__link a {
    text-decoration: none;
    font-family: var(--fuente2);
    font-weight: lighter;
    font-size: 1.2em;
    color: var(--blanco);
    margin: 10px;
}
.sections__items {
    display: flex;
    justify-content: center;
    margin: 10px 0;
    padding: 10px 0;
}
.sections__items div {
    width: 35px;
    height: 35px;
    background-size: 100%;
    background-position: center;
    background-repeat: no-repeat;
    margin: 0 15px;
}
.sections__items .github {
    background-image: url(../../img/github_blanco.png);
}
.sections__items .twitter {
    background-image: url(../../img/twitter-blanco.png);
}
.sections__items .facebook {
    background-image: url(../../img/facebook-blanco.png);
}
.sections__items .linkedin {
    background-image: url(../../img/linkedin-blanco.png);
}

